<template>
  <div class="container" draggable="false" @mousedown="returnFalse">
    <div class="album" ref="album" @mousedown="returnFalse">
      <img src="../../../images/1.jpg" alt width="150" height="200" />
      <img src="../../../images/2.jpg" alt width="150" height="200" />
      <img src="../../../images/3.jpg" alt width="150" height="200" />
      <img src="../../../images/4.jpeg" alt width="150" height="200" />
      <img src="../../../images/5.jpg" alt width="150" height="200" />
      <img src="../../../images/6.png" alt width="150" height="200" />
      <img src="../../../images/7.jpg" alt width="150" height="200" />
      <img src="../../../images/8.jpg" alt width="150" height="200" />
      <img src="../../../images/9.jpg" alt width="150" height="200" />
      <img src="../../../images/10.jpg" alt width="150" height="200" />
    </div>
  </div>
  <!-- 3D
        1 
        2
  3rotatecXrotateY-->
</template>
<script>
export default {
  // methods: {
  //   returnFalse(e) {
  //     e.preventDefault();
  //   }
  // },
  mounted() {
    var album = this.$refs.album;//albumdom
    var imgs = this.$refs.album.children;//album
    var len = imgs.length;
    var unitDeg = 360 / len;
    var newX, newY, lastX, lastY, minusX, minusY;
    var rotX = 0;
    var rotY = 0;
    this.$nextTick(() => {
      Array.prototype.forEach.call(imgs, (it, i, arr) => {
        it.style.transform = `rotateY(${i * unitDeg}deg) translateZ(300px)`;
        it.style.transition = `1s ${(len - i) * 0.1}s`;
        it.onmousedown = this.returnFalse;
      });
      //     albumtransform rotateX  rotateY
      // document.onmousedown = function(e) {
      //   // 
      //   lastX = e.clientX;
      //   lastY = e.clientY;
      //   this.onmousemove = e => {
      //     // 
      //     newX = e.clientX;
      //     newY = e.clientY;

      //     //    
      //     minusX = newX - lastX;
      //     minusY = newY - lastY;

      //     rotX += minusY;
      //     rotY += minusX;

      //     album.style.transform = `rotateX(${-rotX * 0.3}deg) rotateY(${rotY *
      //       0.3}deg)`;

      //     // 
      //     lastX = newX;
      //     lastY = newY;
      //   };
      //   this.onmouseup = e => {
      //     // 
      //     console.log("");
      //     this.onmousemove = null;
      //   };
      // };
    });
  }
};
</script>

<style>
.container {
  background: purple;
  perspective: 1000px; /**/
}
/* &:before {
    content: "";
    display: table;
  } */
.album {
  width: 150px;
  height: 200px;
  /* border: solid 1px red; */
  margin: 200px auto;
  position: relative;
  transform-style: preserve-3d; /**/
}
img {
  position: absolute;
  -webkit-box-reflect: below 5px -webkit-linear-gradient(top, rgba(250, 250, 250, 0), rgba(
          250,
          250,
          250,
          0
        )
        30%, rgba(250, 250, 250, 0.3));
}
</style>


