<template>
  <el-main>
    <el-table :data="tableData">
      <el-table-column prop="id" label="id" width="140"></el-table-column>
      <el-table-column prop="date" label="" width="140"></el-table-column>
      <el-table-column prop="name" label="" width="120"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="scope">
          <el-input v-model="search" size="mini" placeholder="" />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-main>
</template>

<script>
export default {
  data() {
    const i=0;
    const item = {
      id: 1,
      date: "2016-05-02",
      name: "",
      address: " 1518 "
    };
    return {
      tableData: Array(20).fill(item),
      isCollapse: true,
      search: ""
    };
  }
};
</script>

<style>

</style>


